<template>
   <div style="width: 100%">
       <v-toolbar dark color="teal lighten-2">
           <v-toolbar-title class="white--text">Importação</v-toolbar-title>
           <v-spacer></v-spacer>

           <v-toolbar-title class="white--text" style="font-size: 12px; cursor: pointer"  @click="btnDownload">Download planilha padrão</v-toolbar-title>
           <v-btn icon @click="btnDownload">
               <v-icon>arrow_downward</v-icon>
           </v-btn>
       </v-toolbar>
       <v-flex>
           <div style="width: 100%">
               <v-text-field label="Selecione uma planilha" @click='pickFile' v-model='fileName' prepend-icon='attach_file' style="float: left; width: 600px">
               </v-text-field>
               <v-spacer></v-spacer>
               <v-btn @click.prevent="pickFile" style="float: left" color="info">Selecionar</v-btn>

               <v-spacer></v-spacer>
               <input ref="fileUpload" name="fileUpload" type="file" accept=".xlsx" style="display: none" @change="onFilePicked">
               <v-btn @click.prevent="sendUpload" color="success">Importar</v-btn>
           </div>

       </v-flex>
       <v-content>
           <v-container fluid>
               <v-dialog v-model="dialog" max-width="400">
                   <v-card>
                       <v-card-title>
                           <span>Importação concluida com sucesso!</span>
                           <v-btn flat color="green">Download</v-btn>
                       </v-card-title>
                   </v-card>
               </v-dialog>
           </v-container>
       </v-content>


       <v-dialog v-model="loading"
               hide-overlay
               persistent
               width="300">
           <v-card color="primary" dark>
               <v-card-text>
                   Please stand by
                   <v-progress-linear
                           indeterminate
                           color="white"
                           class="mb-0"
                   ></v-progress-linear>
               </v-card-text>
           </v-card>
       </v-dialog>

   </div>
</template>

<script>
    export default {
        name: "IndexImportacaoComponent",
        data() {
            return {
                loading         : false,
                btnLoadin       : false,
                tipoAgenda      : [],
                msgError        : "",
                headers         : axios.defaults.headers.common,
                file            : '',
                fileName        : '',
                idTipoAgenda    : '',
                uploadPorcent   : 0,
                dialog: false,
                fileUrl: '',
                produtos: []
            }
        },
        methods:{
            pickFile () {
                this.$refs.fileUpload.click ()
            },
            onFilePicked (e) {
                const files = e.target.files
                if(files[0] !== undefined) {
                    this.fileName = files[0].name
                    if(this.fileName.lastIndexOf('.') <= 0) {
                        return
                    }
                    const fr = new FileReader ()
                    fr.readAsDataURL(files[0])
                    fr.addEventListener('load', () => {
                        this.fileUrl = fr.result
                        this.file = files[0] // this is an image file that can be sent to server...
                    })
                } else {
                    this.fileName = ''
                    this.file = ''
                    this.fileUrl = ''
                }
            },
            btnDownload()
            {
               // this.loading = true;
                axios({
                    method      :'post',
                    url         :'download-planilha-padrao',
                    responseType:'arraybuffer',
                })
                    .then(function(response)
                    {
                        this.loading = false;
                        this.$forceDownload(response,'consultaPreco.xlsx');
                    }.bind(this))
                    .catch(function () {
                        //this.loading = false;

                    }).then(function () {
                   // this.loading = false;
                }.bind(this));
            },

            btnDownload2()
            {
                // this.loading = true;
                axios({
                    method      :'post',
                    url         :'download-consultas',
                    responseType:'arraybuffer',
                })
                    .then(function(response)
                    {
                        this.loading = false;
                        this.$forceDownload(response,'consultas.xlsx');
                    }.bind(this))
                    .catch(function () {
                        //this.loading = false;

                    }).then(function () {
                    // this.loading = false;
                }.bind(this));
            },

            sendUpload(){
                this.loading = true;
                let formData = new FormData();
                formData.append('fileUpload',this.file);
                axios.post('importar', formData,{headers: {
                    'Content-Type': 'multipart/form-data'
                }})
                    .then(function (response)
                    {
                        this.produtos = response.data.matriz;
                        this.loading = false;
                        this.dialog = true;

                    }.bind(this))
                    .catch(function (error)
                    {
                        this.loading = false;
                    }.bind(this))
                    .then(function ()
                    {
                        this.loading = false;
                        this.btnLoadin = false;
                    }.bind(this))
            },

        },
    }
</script>

