<template>
    <div style="width: 100%">

        <v-toolbar dark color="teal lighten-2">
            <v-toolbar-title class="white--text">Filtros</v-toolbar-title>

            <v-btn icon large @click="listar">
                <v-icon>refresh</v-icon>
            </v-btn>
            <v-btn icon large @click="active">
                <v-icon>beenhere</v-icon>
            </v-btn>
            <v-btn icon large @click="destroy">
                <v-icon>cancel</v-icon>
            </v-btn>
            <v-menu bottom right offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn dark icon v-on="on">
                        <v-icon>more_vert</v-icon>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-tile v-for="(item, i) in dropdownMenu" :key="i" @click="">
                        <v-list-tile-title @click="pesquisa(item.value)">{{ item.title }}</v-list-tile-title>
                    </v-list-tile>
                </v-list>
            </v-menu>
            <v-text-field v-model="form.search" @change="pesquisa('')" append-icon="search" label="Pesquisar" single-line hide-details></v-text-field>
        </v-toolbar>

        <v-data-table
                v-model="selected"
                :headers="headers"
                :items="desserts"
                :pagination.sync="pagination"
                select-all
                item-key="id"
                class="elevation-1"
                :loading="loading">
            <template v-slot:headers="props">
                <tr>
                    <th>
                        <v-checkbox :input-value="props.all"
                                :indeterminate="props.indeterminate"
                                primary
                                hide-details
                                @click.stop="toggleAll">
                        </v-checkbox>
                    </th>
                    <th v-for="header in props.headers"
                        class="text-xs-left"
                        :key="header.text"
                        :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                        @click="changeSort(header.value)">
                        <v-icon small>arrow_upward</v-icon>
                        {{ header.text }}
                    </th>
                </tr>
            </template>
            <template v-slot:items="props">
                <tr :active="props.selected" @click="props.selected = !props.selected">
                    <td>
                        <v-checkbox :input-value="props.selected" primary hide-details></v-checkbox>
                    </td>
                    <td class="text-xs-left">
                        <div style="height: 20px; margin-right: 5px; padding: 2px; background-color: green; float: left" v-if="props.item.active === 1">

                        </div>
                        <div style="height: 20px; margin-right: 5px; padding: 2px; background-color: red; float: left" v-if="props.item.active !== 1">

                        </div>
                        {{ props.item.id }}
                    </td>
                    <td class="text-xs-left">{{ props.item.local }}</td>
                    <td class="text-xs-left">{{ props.item.endereco }}</td>
                    <td class="text-xs-left">{{ props.item.user.name }}</td>
                    <td class="text-xs-left">{{ props.item.updated_at }}</td>
                </tr>
            </template>
        </v-data-table>
    </div>

</template>

<script>
    export default {
        name: "IndexFiltrosComponent",
        data: () => ({
            loading : false,
            showDialog: false,
            pagination: {
                sortBy: 'name'
            },
            form:{
                active : '',
                search : '',
            },

            selected: [],
            headers: [
                { text: 'ID', value: 'id'},
                { text: 'Local', value: 'local'},
                { text: 'Endereco', value: 'endereco' },
                { text: 'Usuario',  value: 'user' },
                { text: 'Data',  value: 'data' },
            ],
            desserts: [],
            dropdownMenu:[
                {
                    title: 'Ativados', value : '1'
                },
                {
                    title: 'Desativados',  value : '2'
                },
            ]

        }),
        methods:{

            pesquisa(active){
                this.form.active = active;
                this.loading = true;
                axios.post('pesquisa-enderecos',this.form).then(function (result)
                {
                    this.selected = [];
                    this.desserts = result.data;
                    this.loading = false;
                }.bind(this))
                .catch(function (error)
                {
                    this.loading = false;
                }.bind(this));
            },

            listar(){
                axios.post('listar-enderecos').then(function (result)
                {
                    this.selected = [];
                    this.desserts = result.data;
                }.bind(this))
                .catch(function (error)
                {

                }.bind(this));

            },
            destroy(){
                axios.post('destroy-enderecos',{list: this.selected}).then(function (result)
                {
                    this.selected = [];
                    this.listar();
                }.bind(this))
                .catch(function (error)
                {

                }.bind(this));

            },
            active(){
                axios.post('active-enderecos',{list: this.selected}).then(function (result)
                {
                    this.selected = [];
                    this.listar();
                }.bind(this))
                    .catch(function (error)
                    {

                    }.bind(this));

            },
            toggleAll () {
                if (this.selected.length){
                    this.selected = []
                }
                else{
                    this.selected = this.desserts.slice()
                }
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            }
        },
        mounted() {
            this.listar();
        }
    }
</script>
<style scoped>

</style>