<template>
    <div style="width: 100%">

        <v-toolbar dark color="teal lighten-2">
            <v-toolbar-title class="white--text">Consultas rapidas</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="search" label="Pesquisar" single-line hide-details @change="listar"></v-text-field>
        </v-toolbar>

        <v-data-table
                v-model="selected"
                :headers="headers"
                :items="desserts"
                :pagination.sync="pagination"
                select-all
                item-key="id"
                class="elevation-1"
                :loading="loading"
                no-data-text="Nenhum produto encontrato">
            <template v-slot:headers="props">
                <tr>
                    <th v-for="header in props.headers"
                        class="text-xs-left"
                        :key="header.text"
                        :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                        @click="changeSort(header.value)">
                        <v-icon small>arrow_upward</v-icon>
                        {{ header.text }}
                    </th>
                </tr>
            </template>
            <template v-slot:items="props">
                <tr>
                    <td class="text-xs-center" style="border: 1px solid grey;" nowrap>{{ props.item.codigo_barra }}</td>
                    <td class="text-xs-center" style="border: 1px solid grey" nowrap>{{ props.item.name }}</td>
                    <td class="text-xs-center" style="border: 1px solid grey" v-for="value in props.item.valores"
                        :style="{color: value.color, cursor: 'pointer'}"
                        @click="openModalEndereco.showDialog = !openModalEndereco.showDialog;
                        openModalEndereco.endereco = value.endereco">
                        {{ value.valor }}
                    </td>
                </tr>
            </template>
        </v-data-table>

        <v-dialog v-model="openModalEndereco.showDialog" max-width="500px">
            <v-card>
                <v-card-title>
                    <span>Endereço</span>
                    <v-spacer></v-spacer>
                </v-card-title>
                <v-card class="elevation-2">
                    <v-card-text>
                        {{openModalEndereco.endereco}}
                    </v-card-text>
                    <v-spacer></v-spacer>
                    <v-card-actions>
                        <v-btn color="primary" flat @click="openModalEndereco.showDialog=false">Fechar</v-btn>
                    </v-card-actions>
                </v-card>

            </v-card>
        </v-dialog>

    </div>

</template>

<script>
    export default {
        name: "ConsultaRapidaComponent",

        data: () => ({
            openModalEndereco:
                {
                    showDialog: false,
                    endereco : '',
                },
            loading : false,

            pagination: {
                sortBy: 'name'
            },
            search : '',
            selected: [],
            headers: [],
            desserts: []
        }),
        methods:{

            listar(){
                this.loading = true;
                this.desserts = [];
                this.headers  = [];
                axios.post('consulta-rapida',{search: this.search}).then(function (result)
                {
                    this.desserts = result.data.rows;
                    this.headers  = result.data.headers;
                    this.loading = false;
                }.bind(this))
                    .catch(function (error)
                    {
                        this.loading = false;
                    }.bind(this));
            },
            destroy(){
                axios.post('destroy-configuracao',{list: this.selected}).then(function (result)
                {
                    this.listar();
                }.bind(this))
                    .catch(function (error)
                    {

                    }.bind(this));

            },
            toggleAll () {
                if (this.selected.length){
                    this.selected = []
                }
                else{
                    this.selected = this.desserts.slice()
                }
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            }
        },
        mounted(){
        }
    }
</script>
<style scoped>

</style>