<template>
    <div>
        <v-dialog v-model="showDialog" persistent max-width="800px">
            <v-card>
                <v-card-title class="grey lighten-4 py-4 title">
                   Adicionar Produto
                </v-card-title>
                <v-container grid-list-sm class="pa-4">
                    <v-layout row wrap>
                        <v-flex xs6>
                            <v-text-field v-model="form.codigo_barra" prepend-icon="place" placeholder="Codigo Barra"></v-text-field>
                        </v-flex>
                        <v-flex xs6>
                            <v-text-field v-model="form.name" prepend-icon="person_pin_circle" placeholder="Produto"></v-text-field>
                        </v-flex>
                    </v-layout>
                </v-container>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn flat color="error" @click="showDialog = false">Cancelar</v-btn>
                    <v-btn flat  color="success" @click="submit">Salvar</v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    export default {
        name: "IndexConfiguracaoComponent",
        data: () => ({
            form :{
                codigo_barra: '',
                name: '',
            },
            showDialog: false,
        }),
        props: {
            dialog: Boolean
        },
        methods:{
          submit(){
              axios.post('novo-produto', this.form).then(function (result)
              {
                  this.showDialog = false;
              }.bind(this))
                  .catch(function (error)
                  {
                      this.showDialog = false;
                   }.bind(this));

          },
        },
        watch:{
            dialog(){
                this.showDialog = this.dialog;
            }
        }
    }
</script>

