<template>
    <div style="width: 100%">

        <v-toolbar dark color="teal lighten-2">
            <v-toolbar-title class="white--text">Consultas geradas</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="search" label="Pesquisar" single-line hide-details @change="listar"></v-text-field>
        </v-toolbar>

        <v-data-table
                v-model="selected"
                :headers="headers"
                :items="desserts"
                :pagination.sync="pagination"
                select-all
                item-key="id"
                class="elevation-1"
                :loading="loading"
                no-data-text="Nenhum produto encontrato">
            <template v-slot:headers="props">
                <tr>
                    <th>
                        <v-checkbox :input-value="props.all"
                                    :indeterminate="props.indeterminate"
                                    primary
                                    hide-details
                                    @click.stop="toggleAll">
                        </v-checkbox>
                    </th>
                    <th v-for="header in props.headers"
                        class="text-xs-left"
                        :key="header.text"
                        :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                        @click="changeSort(header.value)">
                        <v-icon small>arrow_upward</v-icon>
                        {{ header.text }}
                    </th>
                </tr>
            </template>
            <template v-slot:items="props">
                <tr>
                    <td>
                        <v-checkbox :input-value="props.selected" primary hide-details></v-checkbox>
                    </td>
                    <td class="text-xs-center">
                        {{ props.item.id }}
                    </td>
                    <td class="text-xs-center" nowrap>{{ props.item.codigo_barra }}</td>
                    <td class="text-xs-center" nowrap>{{ props.item.name }}</td>
                    <td class="text-xs-center" v-for="value in props.item.company">
                        {{ value.valor}}
                        <v-btn icon large @click="openModalEndereco.showDialog = !openModalEndereco.showDialog; openModalEndereco.idEndereco = value.id">
                            <v-icon>place</v-icon>
                        </v-btn>
                    </td>

                </tr>
            </template>
        </v-data-table>

        <endereco-component
        :dialog="openModalEndereco.showDialog"
        :id-endereco="openModalEndereco.idEndereco"/>

    </div>

</template>

<script>
    import EnderecoComponent from './EnderecoComponent'
    export default {
        name: "TableComponent",

        components:{
            EnderecoComponent
        },

        data: () => ({
            openModalEndereco:
                {
                    showDialog: false,
                    idEndereco : '',
                },
            loading : false,

            pagination: {
                sortBy: 'name'
            },
            search : '',
            selected: [],
            headers: [],
            desserts: []
        }),
        methods:{

            listar(){
                this.loading = true;
                axios.post('listar-produtos',{search: this.search}).then(function (result)
                {
                    this.desserts = result.data.desserts;
                    this.headers  = result.data.headers;
                    this.loading = false;
                }.bind(this))
                .catch(function (error)
                {
                    this.loading = false;
                }.bind(this));
            },
            destroy(){
                axios.post('destroy-configuracao',{list: this.selected}).then(function (result)
                {
                    this.listar();
                }.bind(this))
                .catch(function (error)
                {

                }.bind(this));

            },
            toggleAll () {
                if (this.selected.length){
                    this.selected = []
                }
                else{
                    this.selected = this.desserts.slice()
                }
            },
            changeSort (column) {
                if (this.pagination.sortBy === column) {
                    this.pagination.descending = !this.pagination.descending
                } else {
                    this.pagination.sortBy = column
                    this.pagination.descending = false
                }
            }
        },
        mounted(){
            this.listar();
        }
    }
</script>
<style scoped>

</style>