import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Dashboard/IndexComponent'
import Importacao from '../components/Importacao/IndexImportacaoComponent'
import Filtros from '../components/Configuracao/IndexFiltrosComponent'
import ConsultasGeradas from '../components/ConsultasGeradas/TableComponent'
import ConsultasRapidas from '../components/Consulta/ConsultaRapidaComponent'
Vue.use(Router);

const routes = [
  {
    path: '/dashboard', name: 'dashboard', component: Home,
    children: [
                {path      : 'importacao', component : Importacao, name : 'importacao'},
                {path      : 'filtros', component : Filtros, name : 'filtros'},
                {path      : 'consultas-geradas', component : ConsultasGeradas, name : 'consultasGeradas'},
                {path      : 'consultas-rapidas', component : ConsultasRapidas, name : 'consultasRapidas'},
        ]
  }
];

export default new Router({
  mode: 'history',
  routes
})

