<template>
    <div>
        <v-dialog v-model="dialog" max-width="500px">
            <v-card>
                <v-card-title>
                    <span>Endereço</span>
                    <v-spacer></v-spacer>
                </v-card-title>
                <v-card class="elevation-2">
                    <v-card-title class="headline">
                        {{endereco.local}}
                    </v-card-title>
                    <v-card-text>
                        {{endereco.endereco}}
                    </v-card-text>
                    <v-spacer></v-spacer>
                    <v-card-actions>
                        <v-btn color="primary" flat @click="dialog=false">Fechar</v-btn>
                    </v-card-actions>
                </v-card>

            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    export default {
        name: "EnderecoComponent",
        data: () => ({
            endereco: '',
            showDialog: false,
        }),
        props: {
            dialog: Boolean,
            idEndereco : String
        },
        methods:{
            getEndereco(){
                axios.post('endereco', {idEndereco : this.idEndereco}).then(function (result)
                {
                    this.endereco = result.data;
                }.bind(this))
                    .catch(function (error)
                    {

                    }.bind(this));

            },
        },
        watch:{
            dialog(){
                this.showDialog = this.dialog;
                this.getEndereco();
            }
        }
    }
</script>

