<template>
    <div>
        <v-toolbar :clipped-left="$vuetify.breakpoint.lgAndUp" color="teal darken-1" dark app fixed>
            <v-toolbar-title style="width: 300px" class="ml-0 pl-3">
                <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
                <span class="hidden-sm-and-down">Menor Preço</span>
            </v-toolbar-title>
            <!--
            //BARRA DE PESQUISA
            <v-text-field flat solo-inverted
                          hide-details
                          prepend-inner-icon="search"
                          label="Search"
                          class="hidden-sm-and-down">
            </v-text-field>
            -->
            <v-spacer></v-spacer>
            <v-menu bottom right offset-y>
                <template v-slot:activator="{ on }">
                    <v-btn dark icon v-on="on">
                        <v-avatar size="32px" tile>
                            <img :src="images.logo" alt="Vuetify">
                        </v-avatar>
                    </v-btn>
                </template>
                <v-list>
                    <v-list-tile v-for="(item, i) in dropdownMenu" :key="i" @click="">
                        <v-list-tile-title @click="logout">{{ item.title }}</v-list-tile-title>
                    </v-list-tile>
                </v-list>
            </v-menu>
        </v-toolbar>
        <v-navigation-drawer v-model="drawer" :clipped="$vuetify.breakpoint.lgAndUp" fixed app>
            <v-list dense>
                <template v-for="item in stateMenuTreeView">
                    <v-layout v-if="item.heading" :key="item.heading" row align-center>
                        <v-flex xs6>
                            <v-subheader v-if="item.heading">
                                {{ item.heading }}
                            </v-subheader>
                        </v-flex>
                        <v-flex xs6 class="text-xs-center">
                            <a href="#!" class="body-2 black--text">EDIT</a>
                        </v-flex>
                    </v-layout>
                    <v-list-group
                            v-else-if="item.children"
                            :key="item.text"
                            v-model="item.model"
                            :prepend-icon="item.model ? item.icon : item['icon-alt']"
                            append-icon="">
                        <template v-slot:activator>
                            <v-list-tile>
                                <v-list-tile-content>
                                    {{ item.text }}
                                </v-list-tile-content>
                            </v-list-tile>
                        </template>
                    </v-list-group>
                    <v-list-tile v-else :key="item.text" @click="router(item.route)">
                        <v-list-tile-action>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                                <v-list-tile-title style="text-decoration: none">
                                    {{ item.text }}
                                </v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </template>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
    export default {
        data: () => ({
            drawer: null,
            images: {
                logo: require('@/assets/static/imagens/cart.svg')
            },
            dropdownMenu:[
                {
                    title: 'Logout', value : '/logout'
                }
            ]

        }),
        methods:{
            router(route){
                this.$router.push({ name: route});
            },
            logout(){
                axios.post('/logout').then(function (result)
                {
                    window.location.reload();
                }.bind(this))
                .catch(function (error)
                {

                }.bind(this));
            },

        },
        computed:{
            stateMenuTreeView () {
                return this.$store.state.menuTreeView
            },
        }
    }
</script>

<style scoped>

</style>